html,
body {
  height: 100%;
}
body {
  font-family: "Roboto", sans-serif;
  display: flex;
  /* color: "purple";
  background-color: black; */
  flex-direction: column;
}
#root {
  flex: 1 0 auto;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

#adsense {
  position:absolute;
  left:0;
  bottom:0;
  right:0;
}

#container {
  height: calc(100% - 60px);
}
